*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  font-size: 1px;
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  line-height: 1.15; }

body {
  margin: 0;
  min-height: 100vh; }

h1, h2, h3, h4, h5, h6, p {
  margin: 0 0 1em; }

textarea {
  vertical-align: top; }

article, aside, footer, header, nav, section, figcaption, figure, main {
  display: block; }

[tabindex="-1"]:focus {
  outline: 0 !important; }

* {
  font-size: 14rem; }

h1 {
  font-size: 26rem; }

h2 {
  font-size: 24rem; }

h3 {
  font-size: 22rem; }

h4 {
  font-size: 20rem; }

h5 {
  font-size: 18rem; }

h6 {
  font-size: 16rem; }

b,
strong {
  font-weight: bolder; }

small {
  font-size: 80%; }

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

table {
  border-collapse: collapse; }

[hidden] {
  display: none !important; }

body {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; }

main {
  padding: 10vh 10vw;
  background-color: #6be6ea;
  font-size: 0;
  box-shadow: 0 0 20px rgba(84, 181, 183, 0.3); }

h1 {
  text-align: center;
  color: #333; }

.description {
  background-color: rgba(255, 255, 255, 0.3);
  padding: 10px;
  margin-bottom: 26rem; }

.burgr-container {
  cursor: pointer;
  display: inline-block;
  background-color: #fff;
  padding: 10px;
  margin-bottom: 8px; }
  .burgr-container:nth-last-child(n+2) {
    margin-right: 8px; }

.burgr-part {
  transition: 300ms; }

.burgr.active .burgr-part {
  background-color: #00a0f4; }
  .burgr.active .burgr-part ::first-letter {
    background-color: red; }
